import React, { Component } from 'react';
import './PortfolioEnergy.css';



class PortfolioEnergy extends Component {
  render() {
    return (
      <div className="PortfolioEnergy">
        <div className="PortfolioEnergy-container">
          <img className="PortfolioEnergy-background" src={this.props.portfolioOngoing.backgroundLarge} alt={this.props.portfolioOngoing.title}></img>
          <div className="PortfolioEnergy-logoContainer">
            <img className="PortfolioEnergy-logo" src={this.props.portfolioOngoing.icon} alt={this.props.portfolioOngoing.title}></img>
            <img className="PortfolioEnergy-background-mobile" src={this.props.portfolioOngoing.background} alt={this.props.portfolioOngoing.title}></img>
          </div>
          <div className="PortfolioEnergy-textContainer">
            <p style={{ color: this.props.colorTitle }} className="PortfolioEnergy-title">
              {this.props.portfolioOngoing.title}
            </p>
            <p style={{ color: this.props.color }} className="PortfolioEnergy-description"> {this.props.portfolioOngoing.description}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default PortfolioEnergy;