import React, { Component } from 'react';
import UnityDescription from "../soft/unityDescription/UnityDescription";
import Service from "../soft/service/Service";
import Technology from "../soft/technology/Technology";
import PortfolioCompleted from "../soft/portfolioCompleted/PortfolioCompleted";
import PortfolioEnergy from "./portfolioEnergy/PortfolioEnergy";
import Event from "../soft/event/Event";
import RedesUnity from "../soft/redesUnity/RedesUnity"
import './Energy.css';
import energyData from 'data/unit/Energy.json';

class Energy extends Component {

  render() {
    return (
      <div className="Energy">
        <UnityDescription unity={energyData} />
        <p style={{ color: energyData.colorTitles }} className="Energy-title-services">SERVICES</p>
        <div className="Energy-container-services">
          <img className="Energy-background11-services" src={"https://drive.google.com/uc?id=1HrohL_VUyjLu6oLEb6C1Zhq8_yPrHVFd"} alt={"background11"}></img>
          <div className="Energy-container-background2-services">
            <img className="Energy-background21-services" src={"https://drive.google.com/uc?id=1ET5xjw7HI9i98nhyuq_P37ClWIfSLJze"} alt={"background21"}></img>
          </div>
          {
            energyData.services.map(
              (service, index) => {
                return <div key={index}  className="Energy-container-service"> 
                <Service 
                service={service} 
                color={energyData.color} 
                colorTitle={energyData.colorSubTitles} 
                marcoServices={energyData.marcoServices} 
                /> 
                </div>
              }
            )
          }
        </div>
        {
          energyData.technologies.map(
            (index) => {
              if(index === 0){
                return <p style={{ color: energyData.colorTitles }} className="Energy-title-services">TECHNOLOGIES <br />USED BY US</p>
              }
            }
          )
        }
        <div className="Energy-container-technologies">
          {
            energyData.technologies.map(
              (technology, index) => {
                return <Technology key={index} technology={technology} color={energyData.color} />
              }
            )
          }
        </div>
        {
          energyData.portfolio.completed.map(
            (index) => {
              if(index === 0){
                return <p style={{ color: energyData.colorTitles }} className="Energy-title-services">PORTFOLIO</p>
              }
            }
          )
        }
         <div className="Energy-container-portfolioCompleted">
          {
            energyData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                if(index === 0){
                return  <>
                        <div className="Energy-container-background1-portfolioCompleted">
                          <img className="Energy-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
                        </div>
                        <img className="Energy-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
                        <p style={{ color: energyData.colorTitles }} className="Energy-completed-title">COMPLETED</p>
                        <PortfolioCompleted 
                        key={index} 
                        index={index} 
                        portfolioCompleted={portfolioCompleted} 
                        color={energyData.color} 
                        colorTitle={energyData.colorSubTitles} 
                        marcoServices={energyData.marcoServices}
                        />
                        </>
                }
                else{
                    return <PortfolioCompleted 
                    key={index} 
                    index={index} 
                    portfolioCompleted={portfolioCompleted} 
                    color={energyData.color} 
                    colorTitle={energyData.colorSubTitles} 
                    marcoServices={energyData.marcoServices}
                    />
                }
              }
            )
          }
        </div>
        <div className="Energy-container-portfolioOngoing">
          {
            energyData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioEnergy 
                key={index} 
                index={index} 
                portfolioOngoing={portfolioOngoing} 
                color={energyData.color} 
                colorTitle={energyData.colorSubTitles} 
                />
              }
            )
          }
        </div>
        {
          energyData.events.map(
            (index) => {
              if(index === 0){
                return <p style={{ color: energyData.colorTitles }} className="Energy-title-services">EVENTS</p>
              }
            }
          )
        }
        <div className="Energy-container-events">
          {
            energyData.events.map(
              (event, index) => {
                return <Event 
                key={index} 
                event={event} 
                color={energyData.color} 
                marcoServices={energyData.marcoServices}
                />
              }
            )
          }
        </div>
        <RedesUnity redes={energyData.redes}/>
      </div>
    );
  }
}

export default Energy;